@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
@font-face {
    font-family: GoogleFont;
    src: url(./fonts/Inter-VariableFont_slnt,wght.ttf);
}

html, body {
    margin: 0;
    background-color: black;
    height: 100%;
    color: white;
    font-family: GoogleFont;
}

b {
    font-weight: 800;
}


@media only screen and (max-width: 768px) {
    /* NAVBAR */

    .NavBar {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 5px 0px;
        /* padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 20px;
        padding-right: 20px; */

        white-space: nowrap;

        /* min-width: 768px; */
        height: 50px;

        background-color:black;
        color: beige;
        opacity: 0.90;

        border-radius: 0 0 20px 20px;

        position: -webkit-sticky;
        position: sticky;
        top: 0;

        transition: all 0.2s;

        z-index: 10;
    }

    .NavBar .pfp {
        display: none;
    }

    .LightMainContent .NavBar {
        background-color: white;

    }

    .DarkMainContent .NavBar:hover {
        background-color: rgb(40, 40, 40);
        box-shadow: 0px 10px 5px 10px rgb(25,25,25);
        opacity: 0.95;
    }
    .LightMainContent .NavBar:hover {
        background-color: beige;
        /* box-shadow: 0px 5px 30px 10px rgb(100,100,100); */
        box-shadow: 0px 5px 10px 10px rgb(200,200,200);
        opacity: 0.95;
    }

    .NavBar img{
        height: 50px;
        width: 50px;
        object-fit: cover;
        border-radius: 25px;
        margin-right: 15px;
    }

    .NavLeft {
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .NavLeft *{
        display: inline-block;
    }

    .NavBar h3{
        margin-right: 0px;
        width: 120px;
    }

    .email {
        padding: 2px;
    }

    .NavBar a{
        /* padding: 2px; */
        /* margin-right: 20px;
        margin-left: 20px; */
        padding: 7px 5px 7px 5px;

        border-bottom: 1px solid transparent;
        border-radius: 5px;
        margin-right: 10px;

        transition: box-shadow 0.2s;
    }

    .NavBar a:link {
        text-decoration: none;
        color: inherit;
    }

    .NavBar a:hover {
        cursor:pointer;
        border-radius: 5px;
    }
    .DarkMainContent .NavBar a:hover {
        box-shadow: 0px 15px 8px -8px black!important;  /*important overrides selected button shadow*/
        border-bottom: 1px solid black;
        /* border-bottom: 2px solid beige;
        border-left: 2px solid beige;
        border-right: 2px solid beige; */
        /* box-shadow: 0 0 10px 5px black !important;  important overrides selected button shadow */
    }
    .LightMainContent .NavBar .nav a:hover {
        box-shadow: 0 0 25px 5px rgb(150,150,150) !important;
    }
    .nav-selected {
        box-shadow: 0px 15px 8px -6px rgb(40, 40, 40) !important;
        border-bottom: 0.5px solid beige !important;
    }

    .ModeBtn {
        font-size: 20px;
        box-shadow: 0 0 50px 1px white;
        background-color: rgb(255,255,255, 0.15);
        border-radius: 250px;
    }
    .LightMainContent .ModeBtn {
        box-shadow: 0 0 40px 2px black;
        background-color: rgb(0,0,0, 0.20);

    }
    .DarkMainContent .ModeBtn:hover {
        box-shadow: 0 0 40px 25px whitesmoke !important;
        /* text-shadow: 0 0 40px yellow; */
        border-radius: 50%;
        background-color: rgb(255,255,255, 0.80);
    }
    .LightMainContent .ModeBtn:hover {
        box-shadow: 0 0 40px 25px black !important;
        border-radius: 50%;
        background-color: rgb(0,0,0, 0.80);
        text-shadow: 0 0 40px yellow;
    }

    .NavBar .blank {
        width: 40%;
    }



    .dropbtn {
        width: 60px;
        height: 40px;
        margin-right: 10px;
        border-radius: 10px;
        background-color: rgb(50,50,50);
        color: beige;
        border: 1px ridge black;
