    /* PROFILE */

    .Profile {        
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;

        margin-top: 10%;
        width: 100%;
        height: 100%;
    }

    .NameInfo {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        width: 80%;

        /* border: 0px solid black; */
    }

    .Profile h1 {
        font-size: 50px;
        margin: 5px;
        text-align: center;
        /* text-indent: 20%; */
    }

