        padding: 0;
        color: inherit;
    }
    .DarkMainContent .NameInfo ul{
        border-top: 2px solid beige;
        border-bottom: 2px solid beige;
    }
    .LightMainContent .NameInfo ul{
        border-top: 2px solid black;
        border-bottom: 2px solid black;
    }

    .NameInfo li{
        display: inline-block;
        margin: 20px;
        margin-left: 40px;
        margin-right: 40px;
    }

    .NameInfo a:link, a:visited {
        text-decoration: none;
        color: inherit;
    }

    .NameInfo a:hover {
        color: rgb(125,125,125);
    }

    .NameInfo i {
        font-size: 30px;
    }


    .Profile .About {
        width: 80%;
        padding: 30px;
        margin-top: 5%;
        text-align: center;
    }

    .About p, .quick-link{
        color: beige;   
        font-weight: 600;
        background-color: black;
        opacity: 80%;
        padding: 30px;

        border-radius: 10px;

        transition: all 0.3s;
    }
    .About p:hover, .quick-link:hover{
        background-color: rgb(50, 50, 50);
        box-shadow: 10px 10px 5px 10px rgb(25,25,25);
    }
    .quick-link {
        cursor: pointer;
    }


