    .Profile .FormalPic {
        /* background: url(./images/formal_pic.jpg) left top no-repeat; */
        margin-left: 80px;
        margin-top: 50px;
        width: auto;
        height: 60vh;
        /* position: absolute; */
        left: 20px;
        /* filter: brightness(90%); */
        /* max-height: 500px; */

        border-radius: 10px;
        box-shadow: 5px 5px 10px 10px rgb(50,50,50);
    }
    .LightMainContent .Profile .FormalPic {
        box-shadow: 5px 5px 10px 10px rgb(150,150,150);

