        width: 95%;    
        min-width: 350px;
        border: 10px solid rgb(25, 25, 25);
        border-radius: 5px;
    }


    /* Experience */

    .ExperienceContainer {
        padding: 10px;
        width: 95% !important;
        min-width: fit-content;

        border: 2px solid transparent;

        transition: all 0.3s;
    }
    .ExperienceContainer:hover {
        background-color: rgb(50, 50, 50);
        box-shadow: 0px 0px 5px 20px rgb(25,25,25);
        border-radius: 10px;

        border-top: 2px solid beige;
        border-bottom: 2px solid beige;

    }

    .ExperienceHeader {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }
    .ExperienceHeader h1{
        margin: 0px;
    }
    .ExperienceHeader h2{
        margin: 10px 0;
    }
    .ExperienceHeader a:link, a:visited{
        color: white;
    }

    .ExperienceInfo li {
        margin: 10px 5px;
    }

    /* Project */

    .Project {
        width: 100%;
    }

    .Project i {
        color: white;
    }

    .ProjectContainer {
        padding: 10px;
        width: 95%;
        min-width: 300px;

        border: 2px solid transparent;

        transition: all 0.3s;
    }
    .ProjectContainer:hover {
        background-color: rgb(50, 50, 50);
