    .SkillItem {
        margin: 10px;
        display: grid;
        grid-template-columns: 20px 40px 20px;
        grid-template-rows: 150px 50px 10px 20px 20px;
        background-color: inherit;
        max-width: 800px;
    }
    /* .SkillItem:hover {
