    } */

    .Bar {
        background-color: beige;
        grid-row: 1/3;
        grid-column: 2;
        box-shadow: 0 0 20px 20px rgb(50,50,50);
        border: 1px solid black;

        position: relative;
        width: 40px;
        left: 0;
        /* left: -80px; */

        cursor: default;

        transition: all 0.3s;

        overflow: hidden;

    }
    /* .BarExpanded {
        position: relative;
        left: -80px;
        width: 200px;
        
        background-color: rgb(245,245,220, 0.3);
        cursor: default;
        border: 1px solid black;

        transition: width 2s;
    } */
    .LightMainContent .Bar {
        background-color: rgb(34, 34, 34);
        /* box-shadow: 0 0 20px 15px rgb(200,200,200); */
        box-shadow: 0 0 20px 15px beige;

    }

    .Bar .info {
        position: absolute;
        top: 0;
        text-align: left;

        width: 180px;

        cursor: default;
        margin: 10px;

        opacity: 0;

    }
    .Bar .info .skillInfoHeader {
        border-bottom: 2px solid white;
    }
    /* .Bar:hover .info {
        opacity: 1;
    } */

    .Fill {
        /* position: absolute;
        bottom:0; */
        background-color: rgb(25,25,25);
        /* height: 100%; */
        z-index: 100;

    }
    .LightMainContent .Fill {
        background-color: rgb(225,225,225);
    }


    .Space {
        border-top: 2px solid beige;
        z-index: 5;
        grid-row: 4;
        grid-column: 1/4;

    }
    .LightMainContent .Space {
        border-top: 2px solid black;
    }

    .Name {
        grid-row: 5;
        grid-column: 2;
        grid-column: 1/4;
        text-align: center;
        
    }

    .tooltip {
        position: relative;
        top: 0;


        display: inline-block;
        border-bottom: 1px dotted black;
        background-color: white;
        width: 160px;
        z-index: 5;
        
        visibility: hidden;
    }

    /* FOOTER */

    .Footer {
        margin-top: 100px;
        height: 10vh;
        padding: 20px;
        text-align: center;
    }
}


