        border-top: 1px solid black;
        box-shadow: 0px 10px 15px 10px rgb(200,200,200);
        opacity: 0.95;
    }

    /* .NavDropdown:hover{
        display: inline-block;
        background-color: white;
    } */

    /* .nav-project {
        position: relative;
    } */
    /* .nav-project:hover .projects-dropdown {
        display: flex;
    } */

    .projects-dropdown {
        position: absolute;
        top: 50px;
        right: 190px;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        width: 160px;

        background-color:rgb(47,47,47);
        color: beige;

        /* border-top: 2px solid beige; */
        /* border-bottom: 2px solid beige; */
        border-radius: 20px;

        box-shadow: 0px 10px 5px 5px rgb(25,25,25);

        z-index: 10;
    }

    .projects-option {
        border-bottom: 1px solid transparent;
        margin: 15px 5px;
    }

    .projects-option:hover {
        box-shadow: 0px 15px 8px -8px black!important;  /*important overrides selected button shadow*/
        border-bottom: 1px solid black;
        /* border-bottom: 2px solid beige; */
        /* border-right: 1px solid beige; */
    }


    .Front {
        min-width: 600px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 20px 50px;
    }

    /* PROFILE */

    .Profile {
        min-height: 100vh;
        padding: 30px;
        
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        min-width: 700px;
    }

    .NameInfo {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        margin-top: 15vh;
        width: 50vw;
        min-width: 300px;
        max-width: 50%;

        /* border: 0px solid black; */
    }

    .Profile h1 {
        font-size: 50px;
        margin: 5px;
        text-align: center;
        /* text-indent: 20%; */
    }

    .NameInfo p{
        text-align: center;
        width: 60%;
        display: flex;
        justify-content: center;
    }

    .NameInfo ul{
        list-style:none;
        padding: 0;
        color: inherit;
    }
    .DarkMainContent .NameInfo ul{
        border-top: 2px solid beige;
        border-bottom: 2px solid beige;
    }
    .LightMainContent .NameInfo ul{
        border-top: 2px solid black;
        border-bottom: 2px solid black;
    }

    .NameInfo li{
        display: inline-block;
        margin: 20px;
        margin-left: 40px;
        margin-right: 40px;
    }

    .NameInfo a:link, a:visited {
        text-decoration: none;
        color: inherit;
    }

    .NameInfo a:hover {
        color: rgb(125,125,125);
    }

    .NameInfo i {
        font-size: 30px;
    }


    .Profile .About {
        max-width: 50%;
        padding: 30px;
        margin-top: 10vh;
    }

    .About p, .quick-link{
        color: beige;   
        font-weight: 600;
        background-color: black;
        opacity: 80%;
        padding: 30px;

        border-radius: 10px;

        transition: all 0.3s;
    }
    .About p:hover, .quick-link:hover{
        background-color: rgb(50, 50, 50);
        box-shadow: 10px 10px 5px 10px rgb(25,25,25);
    }
    .quick-link {
        cursor: pointer;
    }


    /* .shadow
    {
        display:block;
        position:relative;
    }

    .shadow:before
    {
        margin-top: 50px;
        margin-left: 80px;
        display:block;
        content:'';
        position:absolute;
        width: 43vh;
        height:60vh;
        box-shadow:inset 0px 0px 10px 10px black;
    } */

    .Profile .FormalPic {
        /* background: url(./images/formal_pic.jpg) left top no-repeat; */
        margin-left: 80px;
        margin-top: 50px;
        width: auto;
        height: 60vh;
        /* position: absolute; */
        left: 20px;
        /* filter: brightness(90%); */
        /* max-height: 500px; */

        border-radius: 10px;
        box-shadow: 5px 5px 10px 10px rgb(50,50,50);
    }
    .LightMainContent .Profile .FormalPic {
        box-shadow: 5px 5px 10px 10px rgb(150,150,150);

    }


    /* RESUME */

    .Resume {
        text-align: center;
        margin-top: 50px;
    }

    .Resume iframe {
        height: 100vh;
        width: 80vw;    
        border: 10px solid rgb(25, 25, 25);
        border-radius: 5px;
        min-width: 700px;
    }


    /* Experience */

    .ExperienceContainer {
        padding: 20px;
        min-width: fit-content;

        border: 2px solid transparent;

        transition: all 0.3s;
    }
    .ExperienceContainer:hover {
        background-color: rgb(50, 50, 50);
        box-shadow: 0px 0px 5px 20px rgb(25,25,25);
        border-radius: 10px;

        border-top: 2px solid beige;
        border-bottom: 2px solid beige;

    }

    .ExperienceHeader {
        display: flex;
        flex-direction: horizontal;
        justify-content: flex-start;
        align-items: center;
    }
    .ExperienceHeader a:link, a:visited{
        color: white;
    }

    .ExperienceInfo li {
        margin: 10px 5px;
    }

    /* Project */

    .Project i {
        color: white;
    }

    .ProjectContainer {
        padding: 20px;
        min-width: 600px;

        border: 2px solid transparent;

        transition: all 0.3s;
    }
    .ProjectContainer:hover {
        background-color: rgb(50, 50, 50);
        box-shadow: 0px 0px 5px 20px rgb(25,25,25);
        border-radius: 10px;

        border-top: 2px solid beige;
        border-bottom: 2px solid beige;

    }
    .DarkMainContent .ProjectContainer {
        /* border-bottom: 2px solid beige; */
    }
    .LightMainContent .ProjectContainer {
        border-bottom: 2px solid black;
    }

    .ProjectContainer h1 a:link, a:visited{
        color: white;
    }
    .LightMainContent .ProjectContainer h1 a:link, a:visited{
        color: black;
    }

    .LightMainContent .ProjectContainer i {
        color: black;
    }

    .bullets {
        width: 50%;
        margin: 0;
    }

    .ProjectInfo {
        display: flex;
        flex-direction: row;
        justify-content:space-between;
        margin-bottom: 50px;
    }

    .ProjectInfo ul {   
        margin-top: 0;
        min-width: 300px;
    }

    .ProjectInfo li {
        margin: 10px 5px;
    }
    .ProjectInfo li a:link, a:visited {
        text-decoration: none;
        color: inherit;
    }

    .ProjectInfo img {
        margin-left: 50px;
        height: 300px;
        max-width: 500px;
        min-width: 0;
        flex: 0 0 auto;

        box-shadow: 0 0 25px 20px rgb(25, 25, 25);
        border: 2px solid black;
        transition: 0.25s;
    } 
    /* .ProjectInfo img:hover {
        margin-left: 0px;
        height: 500px;
        max-width: 800px;
        transition-duration: 0.5s;
    } */
    .LightMainContent .ProjectInfo img {
        box-shadow: 0 0 25px 25px rgb(175,175,175);
        border: 2px solid white;
    }

    .ProjectInfo .ThisOne{
        margin-left: 50px;
        width: 550px;
        height: 250px;
        min-width: 0;
        flex: 0 0 auto;
        background-color:rgb(75, 75, 75);
        border-radius: 20px;
        text-align: center;
        color: white;

        box-shadow: 0 0 25px 25px rgb(25, 25, 25);
    }
    .LightMainContent .ProjectInfo .ThisOne{
        background-color:rgb(175,175,175);
        box-shadow: 0 0 25px 25px rgb(175,175,175);
        color: black;
    }



    /* Education */

    .EducationContainer {
        padding: 20px;
        min-width: 800px;

        border: 2px solid transparent;

        transition: all 0.3s;
    }
    .EducationContainer:hover {
        background-color: rgb(50, 50, 50);
        box-shadow: 0px 0px 5px 20px rgb(25,25,25);
        border-radius: 10px;

        border-top: 2px solid beige;
        border-bottom: 2px solid beige;
    }
    .LightMainContent .EducationContainer {
        border-bottom: 2px solid black;
    }

    .EducationContainer h1 a:link {
        color: white;
    }
    .LightMainContent .EducationContainer h1 a:link {
        color: black;
    }
    .EducationContainer h1 a:visited {
        color: white;
    }
    .LightMainContent .EducationContainer h1 a:visited {
        color: black;
    }

    .EducationContainer h4, h5 {
        margin: 0;
    }

    .EducationInfo {
        display: flex;
        flex-direction: row;
        justify-content:space-between;
        margin-bottom: 50px;
    }

    .EducationInfo .sub {
        margin-left: 20px;
    }

    .EducationInfo li {
        margin: 10px 5px;
    }

    .EducationInfo img {
        margin-left: 50px;
    }

    .UwLogo {
        height: 250px;
        width: 250px;
        margin-right: 20px;
        box-shadow: 0 0 5px 10px rgb(50, 50, 50);
        border-radius: 125px;
    }

    .RhsLogo {
        height: 250px;
        width: 300px;
    }

    /* SKILLS */

    .Skills {
        align-items: center;
        text-align: center;
        padding: 30px 50px;
    }

    .Skills h1 {
        margin-top: 50px;
    }

    .SkillSection {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    .SkillItem {
        margin: 30px 50px;
        display: grid;
        grid-template-columns: 20px 40px 20px;
        grid-template-rows: 150px 50px 10px 20px 20px;
        background-color: inherit;
        max-width: 800px;
    }
    /* .SkillItem:hover {
        position: relative;
        grid-template-columns: 0 160px 0;
        grid-template-rows: 200px 0 10px 20px 20px;
        
    } */
    /* .SkillItem:hover .tooltip {
        visibility: visible;
    } */

    .Bar {
        background-color: beige;
        grid-row: 1/3;
        grid-column: 2;
        box-shadow: 0 0 20px 20px rgb(50,50,50);
        border: 1px solid black;

        position: relative;
        width: 40px;
        left: 0;
        /* left: -80px; */

        cursor: default;

        transition: all 0.3s;

        overflow: hidden;

    }
    .Bar:hover {
        width: 200px !important;
        left: -80px;

        background-color: rgb(245,245,220, 0.3);
    }
    /* .BarExpanded {
        position: relative;
        left: -80px;
        width: 200px;
        
        background-color: rgb(245,245,220, 0.3);
        cursor: default;
        border: 1px solid black;

        transition: width 2s;
    } */
    .LightMainContent .Bar {
        background-color: rgb(34, 34, 34);
        /* box-shadow: 0 0 20px 15px rgb(200,200,200); */
        box-shadow: 0 0 20px 15px beige;

    }

    .Bar .info {
        position: absolute;
        top: 0;
        text-align: left;

        width: 180px;

        cursor: default;
        margin: 10px;

        opacity: 0;

        transition: all 0.3s;
    }
    .Bar .info .skillInfoHeader {
        border-bottom: 2px solid white;
    }
    .Bar:hover .info {
        opacity: 1;
    }

    .Fill {
        /* position: absolute;
        bottom:0; */
        background-color: rgb(25,25,25);
        /* height: 100%; */
        z-index: 100;

    }
    .LightMainContent .Fill {
        background-color: rgb(225,225,225);
    }


    .Space {
        border-top: 2px solid beige;
        z-index: 5;
        grid-row: 4;
        grid-column: 1/4;

    }
    .LightMainContent .Space {
        border-top: 2px solid black;
    }

    .Name {
        grid-row: 5;
        grid-column: 2;
        grid-column: 1/4;
        text-align: center;
        
    }

    .tooltip {
        position: relative;
        top: 0;


        display: inline-block;
        border-bottom: 1px dotted black;
        background-color: white;
        width: 160px;
        z-index: 5;
        
        visibility: hidden;
    }

    /* FOOTER */

    .Footer {
        margin-top: 100px;
        height: 10vh;
        padding: 20px;
        text-align: center;
    }
}